.pagination {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.pagination__item {
    margin: 0 5px;
    font-size: 12px;
    cursor: pointer;
}

.pagination__item.selected {
    font-size: 18px;
    font-weight: 700;
}

.btn {
    display: block;
    width: 40px;
}