.user__item {
    display: flex;
    align-items: stretch;
    padding: 15px 0;
}

.user__socials {
    flex: 0 1 12%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 15px;
    margin-right: 30px;
}

.user__image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 8px;
}

.user__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.user__content {
    flex: 0 1 88%;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #000;
    border-radius: 10px;
}

.user__content span {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.user__btn {
    width: 70px;
    font-size: 12px;
    padding: 3px 7px;
}